import { Facebook, Instagram, LinkedIn, LocalPhone, LocationOn, MailOutline, Twitter } from "@mui/icons-material"
import  styled  from "styled-components"
import { mobile } from "../responsive"

const Container=styled.div`
    display: flex;
    ${mobile({
     flexDirection: "column"
     })}

`
const Left=styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;

`
const Logo=styled.h1`
   font-weight: bolder;
    
`
const Desc=styled.p`
    margin: 20px 0px;
`
const SocialContainer=styled.div`
    display: flex;
`
const SocialIcon=styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: white;
    background-color: #${props=>props.color};
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
`
const Center=styled.div`
     flex: 1;
     padding: 20px;
     ${mobile({
     display: "none"
     })}

`
const Title=styled.h3`
    margin-bottom: 30px;
`
const List=styled.ul`
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;

`
const ListItem=styled.li`
    width: 50%;
    margin-bottom: 10px;
`
const Right=styled.div`
     flex: 1;
     padding: 20px;
     ${mobile({
     backgroundColor: "#fff8f8"
     })}

`
const ContactItem=styled.div`
margin-bottom: 20px;
display: flex;
align-items: center;
`
const Payment=styled.img`
width:30%;
height: 20%;

`
const Footer = () => {
  return (
    <Container>
     <Left>
     <Logo>PrismCart</Logo>
     <Desc>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quia, possimus! Quibusdam nesciunt itaque assumenda earum modi corrupti vel nobis sunt facere vitae rerum fugit, aperiam perspiciatis eligendi sapiente unde. Labore?</Desc>
     <SocialContainer>
        <SocialIcon color="3B5999">
           <Facebook/> 
        </SocialIcon >
        <SocialIcon color="E4405F">
           <Instagram/> 
        </SocialIcon>
        <SocialIcon color="55ACEE">
        <Twitter/> 
           
        </SocialIcon>
        <SocialIcon color="0e76a8">
        <LinkedIn/> 
        </SocialIcon>
     </SocialContainer>
     </Left>
     <Center>
     <Title>Useful Links</Title>
     <List>
        <ListItem>Home</ListItem>
        <ListItem>Cart</ListItem>
        <ListItem>Man Fashion</ListItem>
        <ListItem>Woman Fashion</ListItem>
        <ListItem>Kids Wear</ListItem>
        <ListItem>Accessories</ListItem>
        <ListItem>My Account</ListItem>
        <ListItem>Order Tracking</ListItem>
        <ListItem>Wishlist</ListItem>
        <ListItem>Terms</ListItem>
     </List>
     </Center>
     <Right>
      <Title>Contact</Title>
      <ContactItem><LocationOn style={{marginRight:"10px"}}/>Hostel K,NIT Jamshedpur</ContactItem>
      <ContactItem><LocalPhone style={{marginRight:"10px"}}/> +91 7755937498</ContactItem>
      <ContactItem><MailOutline style={{marginRight:"10px"}}/> prismcart@nitjsr.ac.in</ContactItem>
      <Payment src="data:image/jpeg;base64,"/>
     </Right>
    </Container>
  )
}

export default Footer